<template>
	<validation-observer ref="observer">
		<v-form id="form">
			<v-row class="mb-1">
				<v-col cols="3">
					<span class="label mb-1">ID шаблона</span>
					<v-text-field
						:readonly="true"
						hide-details="true"
						outlined
						filled
						dense
					/>
				</v-col>
			</v-row>
			<div class="mb-4">
				<span class="label">Название шаблона</span>
				<validation-provider 
					v-slot="{
						classes,
						errors
					}"
					rules="required|min:3|max:60|spaces"
				>
					<v-text-field
						v-model.trim="templateName"
						:class="classes"
						hide-details="true"
						required
						outlined
						filled
						dense
						@input="setName"
					/>
					<transition
						name="fadeHeight"
						mode="out-in"
					>
						<span 
							v-if="errors[0]"
							class="error-message"
						>{{ errors[0] }}</span>
					</transition>
				</validation-provider>
			</div>
			<span class="label">Какой шаблон вы хотите создать</span>
			<v-radio-group
				v-model="type"
				class="mt-0 mb-2"
				hide-details="true"
				column
				@change="setType"
			>
				<v-radio
					class="title mb-0"
					label="Операторский"
					value="OPERATOR"
				/>
				<p class="ml-8 text">
					Текстовый шаблон-скелет для оператора (с необходимым синтаксисом и регулярными выражениями). Регистрация такого шаблона у оператора даст возможность в дальнейшем создать на основе него Пользовательские шаблоны и отправлять сообщения дешево
				</p>
				<v-radio
					class="title mb-0"
					label="Пользовательский"
					value="CUSTOM"	
				/>
				<p class="ml-8 mb-0 text">
					Текстовый шаблон на основе уже зарегистрированного Операторского, в который вы подставите свой текст и переменные и с ним будете отправлять сообщения дешевле
				</p>
			</v-radio-group>
			<section v-if="type === 'OPERATOR'">
				<span class="label mb-2">Введите текст шаблона</span>
				<validation-provider 
					v-slot="{ 
						classes,
						errors
					}"
					ref="editorValidator"
					rules="required"
				>
					<div @mouseup="editorTouch">
						<mc-wysiwyg
							id="template-text-editor"
							v-model="templateText"
							:class="classes"
							height="150"
							@input="formatText"
						/>
					</div>
					<div class="d-flex mt-2 mb-1">
						<EmojiPickerVue @select="emojiSelect" />
						<button
							class="button small-icon ml-3"
							title="Добавить параметр"
							@click.prevent="addParameter"
						>
							<i class="fa-solid fa-code" />
						</button>
					</div>
					<transition
						name="fadeHeight"
						mode="out-in"
					>
						<span
							v-if="errors[0]"
							class="error-message"
						>{{ errors[0] }}</span>
					</transition>
				</validation-provider>
				<span class="mt-4 mb-1 label">Выберите категорию</span>
				<validation-provider 
					v-slot="{ 
						classes,
						errors
					}"
					rules="required"
				>
					<v-select
						v-model="category"
						:class="classes"
						class="mb-2"
						:items="categoryItems"
						:menu-props="{
							closeOnClick: true,
							closeOnContentClick: true,
						}"
						hide-details="true"
						item-text="text"
						item-value="value"
						required
						outlined
						filled
						dense
						@input="setCategory"
					/>
					<transition
						name="fadeHeight"
						mode="out-in"
					>
						<span
							v-if="errors[0]"
							class="error-message"
						>{{ errors[0] }}</span>
					</transition>
				</validation-provider>
				<span class="label mb-1">Выберите язык</span>
				<v-select
					v-model="language"
					class="mb-3"
					:items="languages"
					:menu-props="{
						closeOnClick: true,
						closeOnContentClick: true,
					}"
					hide-details="true"
					outlined
					filled
					dense
					@input="setLanguage"
				/>
				<v-checkbox
					v-model="addHeader"
					class="title mt-0"
					label="Добавить заголовок"
					hide-details
					readonly
					@click="setHeader"
				/>
				<div 
					v-if="addHeader"
					class="ml-8 mt-3"
				>
					<span class="label">Тип заголовка</span>
					<v-select
						v-model="headerType"
						class="mb-1"
						:items="headerItems"
						:menu-props="{
							closeOnClick: true,
							closeOnContentClick: true,
						}"
						hide-details
						item-text="text"
						item-value="value"
						required
						outlined
						filled
						dense
						@input="setHeader"
					/>
					<div v-if="headerType === 'text'">
						<validation-provider 
							v-slot="{
								classes,
								errors
							}"
							rules="required|max:60"
						>
							<span class="label">Контент</span>
							<v-text-field
								v-model.trim="headerText"
								:class="classes"
								class="mb-1"
								hide-details
								outlined
								filled
								dense
								@input="setHeaderText"
							/>
							<transition
								name="fadeHeight"
								mode="out-in"
							>
								<span
									v-if="errors[0]"
									class="error-message"
								>{{ errors[0] }}</span>
							</transition>
						</validation-provider>
					</div>
				</div>
				<v-checkbox
					v-model="addFooter"
					class="title mt-0"
					hide-details="true"
					label="Добавить подпись сообщения"
					@click="setFooter"
				/>
				<div 
					v-if="addFooter"
					class="ml-8 mt-3"
				>
					<validation-provider 
						v-slot="{
							classes,
							errors
						}"
						rules="required|max:60"
					>
						<v-text-field
							v-model.trim="footerText"
							:class="classes"
							hide-details="true"
							outlined
							filled
							dense
							@input="setFooterText"
						/>
						<transition
							name="fadeHeight"
							mode="out-in"
						>
							<span
								v-if="errors[0]"
								class="error-message"
							>{{ errors[0] }}</span>
						</transition>
					</validation-provider>
				</div>
				<v-checkbox
					v-model="addButtons"
					class="title mt-0"
					label="Добавить кнопки"
					hide-details="true"
					@click="setKeyboard"
				/>
				<div v-if="addButtons">
					<v-radio-group
						v-model="buttonsType"
						class="ml-7 mt-2"
						column
					>
						<v-radio
							label="Кнопки чата"
							class="title"
							value="chat"
							@click="setButtons"
						/>
						<div 
							v-if="buttonsType === 'chat'"
							class="ml-8 mb-2"
						>
							<p class="mt-1 mb-0 text">
								Вы можете добавить до 3х кнопок чата в шаблоне. Текст каждой кнопки должен содержать не более 25 символов
							</p>
							<div 
								v-for="(button, index) in storeTemplate.content.keyboard.rows[0].buttons"
								:key="index"
							>
								<div>
									<div class="d-flex justify-space-between">
										<span class="d-flex label align-end">Кнопка {{ index + 1 }}</span>
										<button 
											class="button medium-icon"
											:disabled="disableRemoveButton"
											@click.prevent="removeChatButton(index)"
										>
											<i class="fa-regular fa-trash-can" />
										</button>
									</div>
									<validation-provider 
										v-slot="{
											classes,
											errors
										}"
										rules="required|max:25"
									>
										<span class="label">Название кнопки</span>
										<v-text-field
											v-model="button.name"
											:class="classes"
											class="mb-3"
											hide-details="true"
											outlined
											filled
											dense
										/>
										<transition
											name="fadeHeight"
											mode="out-in"
										>
											<span
												v-if="errors[0]"
												class="error-message"
											>{{ errors[0] }}</span>
										</transition>
									</validation-provider>
									<span class="label">Код кнопки</span>
									<validation-provider 
										v-slot="{
											classes,
											errors
										}"
										rules="required|max:256"
									>
										<v-text-field
											v-model="button.code"
											:class="classes"
											class="mb-3"
											hide-details="true"
											outlined
											filled
											dense
										/>
										<transition
											name="fadeHeight"
											mode="out-in"
										>
											<span
												v-if="errors[0]"
												class="error-message"
											>{{ errors[0] }}</span>
										</transition>
									</validation-provider>
								</div>
							</div>
							<button
								class="button medium"
								:disabled="disableAddButton"
								@click.prevent="addChatButton"
							>
								Добавить кнопку +
							</button>
						</div>
						<v-radio
							class="title ma-0"
							label="Кнопки ссылка/звонок"
							value="action"
							hide-details
							@click="setButtons"
						/>
						<div 
							v-if="buttonsType === 'action'"
							class="ml-8"
						>
							<p class="text mb-0">
								Вы можете добавить в сообщение одновременно 1 кнопку-ссылку и 1 кнопку-звонок. Эти кнопки клиент сможет нажать несколько раз
							</p>
							<v-checkbox
								v-model="addUrlButton"
								label="Кнопка-ссылка"
								class="ma-0"
								hide-details
								@click="setUrlButton"
							/>
							<div
								v-if="addUrlButton" 
								class="ml-8"
							>
								<validation-provider 
									v-slot="{
										classes,
										errors
									}"
									rules="required|max:25"
								>
									<span class="label">Название кнопки</span>
									<v-text-field
										v-model="templateSchema.button.URL.text"
										:class="classes"
										hide-details
										outlined
										filled
										dense
									/>
									<transition
										name="fadeHeight"
										mode="out-in"
									>
										<span
											v-if="errors[0]"
											class="error-message"
										>{{ errors[0] }}</span>
									</transition>
								</validation-provider>
								<validation-provider 
									v-slot="{
										classes,
										errors
									}"
									rules="required|max:2000|url"
								>
									<span class="label">Ссылка кнопки. Максимальная длина ссылки - 2000 символов</span>
									<v-text-field
										v-model="templateSchema.button.URL.url"
										:class="classes"
										hide-details
										outlined
										filled
										dense
									/>
									<transition
										name="fadeHeight"
										mode="out-in"
									>
										<span
											v-if="errors[0]"
											class="error-message"
										>{{ errors[0] }}</span>
									</transition>
								</validation-provider>
								<v-checkbox
									v-model="addDynamicUrl"
									class="label ma-0"
									hide-details
									label="Сделать ссылку кнопки динамической"
								/>
							</div>
							<v-checkbox
								v-model="addPhoneButton"
								class="ma-0"
								hide-details
								label="Кнопка-звонок"
								@click="setPhoneButton"
							/>
							<div 
								v-if="addPhoneButton"
								class="ml-8"
							>
								<validation-provider 
									v-slot="{
										classes,
										errors
									}"
									rules="required|max:25"
								>
									<span class="label">Название кнопки</span>
									<v-text-field
										v-model="templateSchema.button.PHONE.text"
										:class="classes"
										hide-details
										outlined
										filled
										dense
									/>
									<transition
										name="fadeHeight"
										mode="out-in"
									>
										<span
											v-if="errors[0]"
											class="error-message"
										>{{ errors[0] }}</span>
									</transition>
								</validation-provider>
								<validation-provider 
									v-slot="{
										classes,
										errors
									}"
									rules="required|min:4"
								>
									<span class="label">Номер телефона (необходимо указать с + и кодом страны)</span>
									<v-text-field
										v-model="templateSchema.button.PHONE.phone"
										v-mask="'+###############################'"
										:class="classes"
										hide-details
										outlined
										filled
										dense
										@keypress="filter"
									/>
									<transition
										name="fadeHeight"
										mode="out-in"
									>
										<span
											v-if="errors[0]"
											class="error-message"
										>{{ errors[0] }}</span>
									</transition>
								</validation-provider>
							</div>
						</div>
					</v-radio-group>
				</div>
			</section>
			<span class="label mt-2">Оставьте коментарий если нужно</span>
			<v-textarea
				v-model="templateComment"
				:no-resize="true"
				outlined
				filled
				dense
			/>
		</v-form>
	</validation-observer>
</template>

<script>
import EmojiPickerVue from '@/components/EmojiPicker.vue';
import { McWysiwyg } from '@mycure/vue-wysiwyg';
import languages from '@/helpers/languages';
import { mapGetters } from 'vuex';

export default {
	components: {
		EmojiPickerVue,
		McWysiwyg
	},
	data() {
		return {
			subjectIds: [],
			subjectItems: [
				{
					text: 'Edna 1',
					value: 1
				},
				{
					text: 'Edna 2',
					value: 2
				},
				{
					text: 'Edna 2',
					value: 2
				}
			],
			templateName: '',
			addHeader: true,
			headerType: 'text',
			headerText: '',
			headerItems: [
				{
					text: 'Текст',
					value: 'text'
				},
				{
					text: 'Видео',
					value: 'video'
				},
				{
					text: 'Изображение',
					value: 'image',
				},
				{
					text: 'Документ',
					value: 'document'
				}
			],
			languages: languages,
			language: '',
			templateText: '',
			addFooter: false,
			footerText: '',
			type: 'OPERATOR',
			categoryItems: [
				{
					text: 'Служебные сообщения',
					value: 'UTILITY'
				},
				{
					text: 'Авторизационные сообщения',
					value: 'AUTHENTICATION'
				},
				{
					text: 'Маркетинговые сообщения',
					value: 'MARKETING'
				}
			],
			activeFrame: 'android',
			formattedText: '',
			templateComment: '',
			category: '',
			variableCount: 0,
			addButtons: false,
			buttonsType: 'chat',
			addUrlButton: false,
			addDynamicUrl: false,
			addPhoneButton: false,
			chatButtons: [
				{
					text: '',
					buttonType: 'QUICK_REPLY',
					payload: ''
				}
			],
			actionButtons: [],
		};
	},
	computed: {
		...mapGetters([
			'getTemplate',
			'getContentSchema'
		]),
		storeTemplate() {
			return this.getTemplate;
		},
		templateSchema() {
			return this.getContentSchema;
		},
		disableAddButton() {
			return this.chatButtons.length === 3;
		},
		disableRemoveButton() {
			return this.chatButtons.length === 1;
		}
	},
	mounted() {
		this.setHeader();
	},
	methods: {
		subjectSelect() {
			this.$store.commit('setSubjectIds', this.subjectIds);
		},
		setName() {
			this.$store.commit('setTemplateName', this.templateName);
		},
		setType() {
			this.$store.commit('setTemplateType', this.type);
		},
		setLanguage() {
			this.$store.commit('setTemplateLanguage', this.language);
		},
		setCategory() {
			this.$store.commit('setTemplateCategory', this.category);
		},
		setHeader() {
			if(this.addHeader) this.$store.commit('setTemplateHeader', this.templateSchema.headers[this.headerType]);
			else this.$store.commit('removeTemplateHeader');
		},
		setHeaderText() {
			this.$store.commit('setTemplateHeaderText', this.headerText);
		},
		setFooter() {
			if(this.addFooter) this.$store.commit('setTemplateFooter', this.templateSchema.footer);
			else this.$store.commit('removeTemplateFooter');
		},
		setFooterText() {
			this.$store.commit('setTemplateFooterText', this.footerText);
		},
		setKeyboard() {
			if(this.addButtons) {
				this.$store.commit('setTemplateKeyboard', this.templateSchema.keyboard);
				this.setButtons();
			}
			else this.$store.commit('removeTemplateKeyboard');
		},
		setButtons() {
			const buttons = this.buttonsType === 'chat' ? this.chatButtons : this.actionButtons;
			this.$store.commit('setTemplateButtons', buttons);
		},
		addChatButton() {
			this.chatButtons.push(JSON.parse(JSON.stringify(this.templateSchema.button.QUICK_REPLY)));
			this.$store.commit('setTemplateButtons', this.chatButtons);
		},
		removeChatButton(index) {
			this.$store.commit('removeTemplateButton', index);
		},
		setUrlButton() {
			if(this.addUrlButton) this.actionButtons.unshift(this.templateSchema.button.URL);
			else this.actionButtons.shift();
			this.$store.commit('setTemplateButtons', this.actionButtons);
		},
		setDynamicUrl() {},
		setPhoneButton() {
			if(this.addPhoneButton) this.actionButtons.unshift(this.templateSchema.button.PHONE);
			else this.actionButtons.pop();
			this.$store.commit('setTemplateButtons', this.actionButtons);
		},
		emojiSelect(emoji) {
			const textEditor = document.getElementById('editor');
			textEditor.append(emoji.data);
			this.templateText += emoji.data;
			this.formattedText += emoji.data;
			this.$store.commit('setTemplateText', this.formattedText);
		},
		formatText() {
			let text = this.templateText;
			let counter = 1;
			text = text.replace(/\*(.*?)\*/g, '<b>$1</b>');
			text = text.replace(/_(.*?)_/g, '<i>$1</i>');
			text = text.replace(/~(.*?)~/g, '<s>$1</s>');
			text = text.replace(/<span[^>]*>{{(.*?)}}<\/span>/g, () => {
				return `{{${counter++}}}`;
			});
			text = text.replace(/&nbsp;/g, ' ');
			text = text.replace(/```(.*?)```/g, '<span style="font-family: monospace">$1</span>');
			this.formattedText = text;
			this.$store.commit('setTemplateText', this.formattedText);
		},
		addParameter() {
			const editor = document.getElementById('editor');

			const selection = window.getSelection();

			const parameter = document.createElement('span');
			parameter.innerHTML = '{{Строка символов}}';
			parameter.style.cssText += `
				display: inline-block;
				white-space: nowrap;
				padding: 0px 4px;
				border-radius: 4px;
				background-color: rgb(84, 220, 160);
			`;
			parameter.setAttribute('contenteditable', 'false');

			if (editor.contains(selection.anchorNode)) {
				const range = selection.getRangeAt(0);

				range.insertNode(parameter);

				range.setStartAfter(parameter);
				range.collapse(true);
				selection.removeAllRanges();
				selection.addRange(range);

				editor.focus();
			} else {
				editor.appendChild(parameter);
			}
			this.templateText = editor.innerHTML;
			this.formatText();
		},
		editorTouch() {
			this.$refs.editorValidator.validate();
		},
		filter(evt) {
			evt = (evt) ? evt : window.event;
			const expect = evt.target.value.toString() + evt.key.toString();
			
			if (!/^[-+]?[0-9]*\.?[0-9]*$/.test(expect)) {
				evt.preventDefault();
			} else {
				return true;
			}
		}
	}
};
</script>

<style lang="scss" scoped>
	.button {
		font-size: 12px;
		font-weight: 500;
		color: #121212;
		box-shadow: rgb(183, 183, 183) 0px 1px 4px -1px;
		padding: 4px;
		border-radius: 8px;
		transition: all 400ms ease 0s;
		cursor: pointer;
		&.small-icon {
			width: 30px;
			height: 30px;
		}
		&.medium-icon {
			font-size: 14px;
			width: 36px;
			height: 36px;
		}
		&.medium {
			font-size: 14px;
			height: 36px;
			padding: 8px 20px;
		}
		&[disabled] {
			color: rgb(183, 183, 183);
			border-color: rgb(255, 255, 255);
			box-shadow: none;
			transition: all 400ms ease 0s;
			cursor: not-allowed;
			&:hover {
				background-color: transparent;
			}
			&:active {
				background-color: transparent;
				box-shadow: none;
			}
		}
		&:hover {
