<template>
	<div 
		id="new_template"
	>
		<section class="template-create__header mb-5">
			<v-container>
				<h1 class="whatsapp-title">
					Новый WhatsApp шаблон
				</h1>
				<div class="d-flex justify-space-between mt-5">
					<div class="d-flex align-center">
						<i class="whatsapp-icon fa fa-whatsapp" />
						<span class="whatsapp-text">WHATSAPP</span>
						<span class="whatsapp-type">Draft</span>
					</div>
					<button
						class="submit-btn" 
						@click="submit"
					>
						Submit
					</button>
				</div>
			</v-container>
		</section>
		<div class="content">
			<v-container class="d-flex justify-space-between pt-5">
				<section class="form_section">
					<TemplateCreateForm ref="form" />
				</section>
				<section class="phone">
					<TemplateCreatePhone />
				</section>
			</v-container>
		</div>
		<TemplateCreateModalVue 
			ref="modal"
			v-model="openModal"
			title="Добавьте пример контента шаблона"
		>
			<template #content>
				<v-col>
					<v-row>
						<v-col cols="7">
							<TemplateCreateFormAdditional 
								ref="extra_form"
								:is-header="headerExamples"
								:is-text="textExamples"
								:show-buttons="true"
								:header-type="template.content.header?.headerType"
							/>
						</v-col>
						<v-col 
							cols="5"
							class="message-block"
						>
							<div class="message-block-fixed-content">
								<TemplateCreateMessageVue
									class="message-content"
									:template="template"
									:examples="examples"
								/>
							</div>
						</v-col>
					</v-row>
				</v-col>
			</template>
			<template #buttons>
				<v-card-actions>
					<v-spacer />
					<v-btn
						color="grey darken-4"
						text
						@click="close"
					>
						Отмена
					</v-btn>
					<v-btn
						dark
						@click="send"
					>
						Отправить
					</v-btn>
				</v-card-actions>
			</template>
		</TemplateCreateModalVue>
	</div>
</template>

<script>
import TemplateCreateForm from '@/components/TemplateCreateForm.vue';
import TemplateCreatePhone from '@/components/TemplateCreatePhone.vue';
import TemplateCreateModalVue from '@/components/TemplateCreateModal.vue';
import TemplateCreateFormAdditional from '@/components/TemplateCreateFormAdditional.vue';
import TemplateCreateMessageVue from '@/components/TemplateCreateMessage.vue';
import { HTTP } from '@/helpers/httpCommon';
import { mapGetters } from 'vuex';

export default {
	components: {
		TemplateCreateForm,
		TemplateCreatePhone,
		TemplateCreateModalVue,
		TemplateCreateFormAdditional,
		TemplateCreateMessageVue
	},
	data() {
		return {
			openModal: false,
			headerExamples: false,
			textExamples: false
		};
	},
	computed: {
		...mapGetters([
			'getTemplate',
			'getSubjectIds',
			'getTemplateExamples'
		]),
		template() {
			return this.getTemplate;
		},
		subjectIds() {
			return this.getSubjectIds;
		},
		examples() {
			return this.getTemplateExamples;
		}
	},
	methods: {
		async submit() {
			const isValid = await this.$refs.form.$refs.observer.validate();
			if(isValid) {
				this.headerExamples = ['IMAGE', 'VIDEO', 'DOCUMENT'].includes(this.template.content.header?.headerType);
				this.textExamples = /\{\{\d+\}\}/.test(this.template.content.text);
				// this.urlExample = this.template.content.keyboard.rows.buttons.some(item => 'urlDynamic' in item)
				if(this.headerExamples || this.textExamples) {
					this.openModal = true;
				} else {
					const messageMatcher = {
						messageMatcher: this.template,
						subjectIds: [743]
					};
					console.log(messageMatcher);
					this.fetch(messageMatcher);
				}
			}
		},
		close() {
			this.$refs.modal.close();
		},
		fetch(data) {
			HTTP.post('/create/message/matcher', data)
				.then((response) => {
					console.log(response);
					this.$toast.success('Шаблон успешно создан!', {
						timeout: 2000
					});
					this.$router.push('/templates');
				})
				.catch(error => {
					this.$toast.error(error.message, {
						timeout: 2000
					});
				});
		},
		async send() {
			const messageMatcher = {
				messageMatcher: this.template,
				subjectIds: [743]
			};
			if(this.$refs.extra_form.fileUrl) {
				messageMatcher.messageMatcher.content.header.headerExampleMediaUrl = this.$refs.extra_form.fileUrl;
			}
			if(this.$refs.extra_form.files) {
				messageMatcher.messageMatcher.content.header.originalFileName = this.$refs.extra_form.files.name;
				const base64String = await this.readFileAsBase64(this.$refs.extra_form.files);
				messageMatcher.messageMatcher.content.header.headerExampleMediaUrl = base64String;
			}
			if(this.$refs.extra_form.textExampleParams.length) {
				messageMatcher.messageMatcher.content.textExampleParams = this.$refs.extra_form.textExampleParams;
			}
			console.log(messageMatcher);

			this.$refs.modal.close();

			this.fetch(messageMatcher);
		},
		async readFileAsBase64(file) {
			return new Promise((resolve, reject) => {
				const reader = new FileReader();

				reader.onload = () => {
					const base64String = reader.result.split(',')[1];
					resolve(base64String);
				};

				reader.onerror = () => {
					reject(reader.error);
				};

				reader.readAsDataURL(file);
			});
		}
	},
};
</script>

<style lang="scss" scoped>
	.whatsapp-title {
		color: white;
		font-size: 30px;
	}
	.whatsapp-icon {
		margin-right: 10px;
		padding: 7px;
		background-color: #25D266;
		color: white;
		border-radius: 50%;
		font-size: 20px;
	}
	.whatsapp-text {
		margin-right: 10px;
		color: white;
	}
	.whatsapp-type {
		padding: 0px 10px;
		font-weight: 500;
		color: #25D266;
		border: 2px solid #25D266;
		border-radius: 20px;
	}
	.submit-btn {
		padding: 8px 25px;
		font-size: 14px;
		background: #000000;
		font-weight: 600;
		color: white;
		border-radius: 10px;
		transition: all 400ms ease 0s;
	}
	.submit-btn:hover {
		color: rgb(183, 183, 183);
		transition: all 400ms ease 0s;
	}
	#new_template {
		color: black;
	}
	.content {
		background-color: white;
	}
	.form_section {
		width: 100%;
		max-width: 600px;
	}
	.phone {
		display: flex;
		flex-direction: column;
		min-width: 320px;
		position: sticky;
		top: 100px;
		left: 48px;
		width: 320px;
		height: max-content;
		align-items: flex-end;
		margin-left: 16px;
	}
	.message-block {
		background: rgb(230, 220, 212);
	}
	.message-block-fixed-content {
		display: flex;
		min-width: 100%;
		flex-direction: column;
		position: sticky;
		top: 50px;
		left: 48px;
		height: -moz-max-content;
		height: max-content;
		align-items: flex-end;
	}
	.message-content {
		min-width: 100%;
	}
</style>