<template>
	<div 
		v-on-clickaway="away"
		class="emoji-picker"
	>
		<button 
			class="emoji-picker__button" 
			title="Выбрать emoji"
			@click.prevent="toggle"
		>
			<i class="fa-regular fa-face-smile" />
		</button>
		<transition name="fade">
			<VEmojiPicker
				v-show="active"
				continuous-list="true"
				class="emoji-picker__content"
				@select="selectEmoji"
			/>
		</transition>
	</div>
</template>

<script>
import { VEmojiPicker } from 'v-emoji-picker';
import { mixin as clickaway } from 'vue-clickaway';
export default {
	components: {
		VEmojiPicker
	},
	mixins: [ clickaway ],
	data() {
		return {
			active: false
		};
	},
	methods: {
		selectEmoji(selectedEmoji) {
			this.$emit('select', selectedEmoji);
		},
		toggle () {
			this.active = !this.active;
		},
		away () {
			this.active = false;
		}
	},
};
</script>

<style lang="scss" scoped>
	.emoji-picker {
		&__button {
			position: relative;
			color: #121212;
			width: 30px;
			height: 30px;
			display: flex;
			-webkit-box-pack: center;
			justify-content: center;
			-webkit-box-align: center;
			align-items: center;
			padding: 4px;
			border-radius: 8px;
			cursor: pointer;
			box-shadow: rgba(18, 18, 18, 0.2) 0px 1px 4px -1px;
			transition: all 400ms ease 0s;
		}
		&__button:hover {
			background-color: rgba(0, 0, 0, 0.1);
			transition: all 400ms ease 0s;
		}
		&__button:active {
			background-color: rgba(0, 0, 0, 0.2);
			box-shadow: rgba(18, 18, 18, 0.2) 0px 1px 4px 3px;
		}
		&__content {
			background-color: rgb(255, 255, 255);
			border-radius: 4px;
			position: absolute;
			margin-top: 5px;
			z-index: 3;
			padding: 0px;
			transition: top 400ms ease 0s;
			box-shadow: rgba(0, 0, 0, 0.22) 0px 4px 16px;
		}
	}
	.fade-enter-active, .fade-leave-active {
		transition: opacity .5s;
	}
	.fade-enter, .fade-leave-to {
		opacity: 0;
	}
</style>