<template>
	<div>
		<div class="phone_message">
			<div v-if="template.content.header">
				<div v-if="template.content.header?.headerType === 'TEXT'">
					<span 
						v-if="template.content.header.text"
						class="phone_message_header_text"
					>{{ template.content.header.text }}</span>
				</div>
				<div
					v-else
					class="phone_message_header_attachment"
				>
					<i 
						v-if="template.content.header?.headerType === 'VIDEO'"
						class="fa-regular fa-file-video"
					/>
					<i 
						v-if="template.content.header?.headerType === 'IMAGE'"
						class="fa-regular fa-image"
					/>
					<i 
						v-if="template.content.header?.headerType === 'DOCUMENT'"
						class="fa-solid fa-paperclip"
					/>
				</div>
			</div>
			<div class="phone_message_content">
				<span
					v-if="template.content.text || examples.textWithParams"
					class="template_text"
					v-html="templateText"
				/>
				<span
					v-if="template.content.footer?.text"
					class="template_signature"
				><br>{{ template.content.footer.text }}</span>
				<span class="phone_message_time">
					<span>12:00</span>
				</span>
			</div>
		</div>
		<div 
			v-for="(button, index) in chatButtons"
			:key="index"
		>
			<div 
				v-if="button.name"
				class="phone_message__button"
			>
				{{ button.name }}
			</div>
		</div>
		<div 
			v-if="urlButton.length && urlButton[0].text"
			class="phone_message__button"
		>
			<i class="fa-solid fa-arrow-up-right-from-square" /> {{ urlButton[0].text }}
		</div>
		<div 
			v-if="phoneButton.length && phoneButton[0].text"
			class="phone_message__button"
		>
			<i class="fa-solid fa-phone" /> {{ phoneButton[0].text }}
		</div>
	</div>
</template>

<script>
export default {
	props: {
		template: {
			type: Object,
			default: () => ({})
		},
		examples: {
			type: Object,
			default: () => ({})
		}
	},
	computed: {
		templateText() {
			return this.examples.textWithParams ? this.examples.textWithParams : this.template.content.text;
		},
		chatButtons() {
			return this.template.content.keyboard?.rows[0].buttons.filter(button => button.buttonType === 'QUICK_REPLY') || [];
		},
		phoneButton() {
			return this.template.content.keyboard?.rows[0].buttons.filter(button => button.buttonType === 'PHONE') || [];
		},
		urlButton() {
			return this.template.content.keyboard?.rows[0].buttons.filter(button => button.buttonType === 'URL') || [];
		},
	}
};
</script>

<style lang="scss" scoped>
	.phone_message__button {
		font-size: 12px;
		line-height: 14px;
		width: 100%;
		text-align: center;
		word-break: break-word;
		margin-top: 2px;
		padding: 8px;
		text-wrap: nowrap;
		color: rgb(26, 171, 252);
		background-color: rgb(247, 247, 247);
		border-radius: 8px;
	}
	.phone {
		&__buttons {
			max-width: max-content;
			margin-bottom: 16px;
			&__wrapper {
				display: flex;
				-webkit-box-pack: start;
				justify-content: flex-start;
				-webkit-box-align: center;
				align-items: center;
				position: relative;
				border-bottom: 5px solid rgb(236, 236, 236);
			}
			&__button_indicator {
				display: flex;
				-webkit-box-pack: center;
				justify-content: center;
				-webkit-box-align: center;
				align-items: center;
				position: relative;
				margin-bottom: -4px;
				padding: 16px;
			}
		}
	}
	.template_text {
		font-size: 12px;
		line-height: 18px;
	}
	.template_signature {
		font-size: 12px;
		line-height: 14px;
		color: rgb(183, 183, 183);
	}
	.phone_message_header_text {
		font-size: 12px;
		line-height: 14px;
		font-weight: 600;
		word-wrap: break-word;
		padding: 8px 0px 0px 8px;
	}

	.phone_message_header_attachment {
		display: flex;
		-webkit-box-pack: center;
		justify-content: center;
		-webkit-box-align: center;
		align-items: center;
		background-color: rgb(243, 249, 253);
		height: 160px;
		border: 8px solid rgb(255, 255, 255);
		border-radius: 12px;
		font-size: 25px;
	}
	.content {
		background-color: white;
	}
	.form_section {
		width: 100%;
		max-width: 600px;
	}
	.phone_section {
		display: flex;
		flex-direction: column;
		min-width: 320px;
		position: sticky;
		top: 100px;
		left: 48px;
		width: 320px;
		height: max-content;
		align-items: flex-end;
		margin-left: 16px;
	}
	.types_buttons {
		max-width: max-content;
		margin-bottom: 16px;
	}
	.types_buttons_container {
		display: flex;
		-webkit-box-pack: start;
		justify-content: flex-start;
		-webkit-box-align: center;
		align-items: center;
		position: relative;
		border-bottom: 5px solid rgb(236, 236, 236);
	}
	.types_buttons_container__btn_indicator {
		display: flex;
		-webkit-box-pack: center;
		justify-content: center;
		-webkit-box-align: center;
		align-items: center;
		position: relative;
		margin-bottom: -4px;
		padding: 16px;
	}
	.phone_frame {
		position: relative;
		display: flex;
		-webkit-box-pack: center;
		justify-content: center;
		-webkit-box-align: center;
		align-items: center;
		flex-direction: column;
		width: 100%;
		min-height: 670px;
		border-radius: 4px;
	}
	.phone_frame_svg {
		width: 106%;
		height: 100%;
		position: absolute;
		top: 0px;
		left: 0px;
		border-radius: 39px;
		color: rgb(230, 220, 212);
	}
	.phone_frame_container {
		width: calc(100% - 23px);
		position: absolute;
		top: 0px;
		left: 0px;
		margin: 39px 0px 0px 11px;
		overflow: hidden;
		height: calc(100% - 66px);
		border-radius: 30px 33px 34px 32px;
	}
	.phone_frame_container_position {
		height: 100%;
		overflow: hidden auto;
		padding: 48px 32px 20px 16px;
	}
	.phone_frame_container_position::-webkit-scrollbar {
		width: 4px;
	}
	.phone_message {
		position: relative;
		width: 100%;
		border-radius: 5px;
		background-color: rgb(255, 255, 255);
	}
	.phone_message::before {
		content: "";
		position: absolute;
		top: -5px;
		left: -5px;
		transform: rotate(135deg);
		border-bottom-right-radius: 3px;
	}
	.phone_message_content {
		position: relative;
		width: 100%;
		min-height: 31px;
		word-break: break-word;
		white-space: pre-wrap;
		display: inline-block;
		padding: 8px;
	}
	.phone_message_time {
		font-size: 12px;
		line-height: 14px;
		position: relative;
		bottom: -4px;
		float: right;
		padding-left: 8px;
		color: rgb(183, 183, 183);
	}
</style>