<template>
	<div class="buttons">
		<div 
			:active="activeButton === 'android'"
			class="buttons__button_indicator"
		>
			<button 
				class="buttons__button"
				@click="changeFrame('android')"
			>
				<div class="buttons__button__icon_container">
					<i class="buttons__button__icon fa-brands fa-android" />
				</div>
				<span class="buttons__button__text">Android</span>
			</button>
		</div>
		<div
			:active="activeButton === 'ios'"
			class="buttons__button_indicator"
		>
			<button
				class="buttons__button"
				@click="changeFrame('ios')"
			>
				<div class="buttons__button__icon_container">
					<i class="buttons__button__icon fa-brands fa-apple" />
				</div>
				<span class="buttons__button__text">iOS</span>
			</button>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			activeButton: 'android'
		};
	},
	mounted() {
		
	},
	methods: {
		changeFrame(type) {
			this.$emit('change', type);
			this.activeButton = type;
		}
	},
};
</script>

<style lang="scss" scoped>
	.buttons {
		display: flex;
		margin-bottom: 15px;
	}
	.buttons__button_indicator {
		padding: 15px;
		padding-top: 0px;
		border-bottom: 5px solid rgb(255, 255, 255, 0.5);
		&[active] {
			border-bottom: 5px solid black;
		}
	}
	.buttons__button {
		display: flex;
		align-items: center;
		padding: 0 9px;
		border-radius: 32px;
		border: 2px solid rgb(183, 183, 183);
	}
	.buttons__button__icon_container {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 20px;
		height: 20px;
		margin-right: 5px;
		border-radius: 50%;
		background-color: black;
	}
	.buttons__button__icon {
		color: #ffffff;
		font-size: 10px;
	}
	.buttons__button__text {
		color: black;
		letter-spacing: -0.5px;
	}
</style>