<template>
	<v-row justify="center">
		<v-dialog
			v-model="dialog"
			persistent
			max-width="1100"
		>
			<v-card>
				<v-toolbar
					dark
					height="50"
					color="primary"
				>
					<v-toolbar-title>{{ title }}</v-toolbar-title>
					<v-spacer />
					<v-btn
						icon
						dark
						@click="close"
					>
						<v-icon>mdi-close</v-icon>
					</v-btn>
				</v-toolbar>
				<slot name="content" />
				<slot name="buttons" />
			</v-card>
		</v-dialog>
	</v-row>
</template>

<script>
export default {
	model: {
		prop: 'value',
		event: 'change'
	},
	props: {
		value: {
			type: Boolean,
			default: false
		},
		title: {
			type: String,
			default: ''
		},
		showButtons: {
			type: Boolean,
			default: false
		},
		onSaveFunction: {
			type: Function,
			default: () => {}
		}
	},
	data() {
		return {
			dialog: this.value	
		};
	},
	watch: {
		value: {
			handler(newValue) {
				this.dialog = newValue;
			}
		}
	},
	methods: {
		handleClick() {
			this.onSaveFunction();
			this.close();
		},
		close () {
			this.dialog = false;
			this.$emit('change', false);
		}
	},
};
</script>